import Axios from "axios";

export const GET_TOKEN = 'GET_TOKEN';

export function getToken() {
    return Axios.get('https://frontend-test-assignment-api.abz.agency/api/v1/token')
        .then(response => response.data)
        .then(data => ({
            type: GET_TOKEN,
            token: data.token
        }));
}

export const GET_USER = 'GET_USER';

export function getUser() {
    return Axios.get('https://frontend-test-assignment-api.abz.agency/api/v1/users/1')
        .then(response => response.data)
        .then(data => ({
            type: GET_USER,
            user: data.user
        }));
}

export const GET_POSITION = 'GET_POSITION';

export function getPosition() {
    return Axios.get('https://frontend-test-assignment-api.abz.agency/api/v1/positions')
        .then(response => response.data)
        .then(data => ({
            type: GET_POSITION,
            position: data.positions
        }));
}

export const GET_OUR_USERS = 'GET_OUR_USERS';

export function getOurUsers(count) {

    return Axios.get(`https://frontend-test-assignment-api.abz.agency/api/v1/users?page=1&count=${count}`)
        .then(response => response.data)
        .then(data => ({
            type: GET_OUR_USERS,
            colleagues: data.users
        }));
}

export const SET_COUNT = 'SET_COUNT';

export function setCount(counter) {
    return {
        type: SET_COUNT,
        counter: counter + 6
    }
}

export const SET_NEW_USER = 'SET_NEW_USER';

export function setNewUser(user) {
    return {
        type: SET_NEW_USER,
        user
    }
}

