import {createStore, applyMiddleware} from 'redux';
import reducer from './reducer';
import promise from 'redux-promise';
import logger from 'redux-logger';

const state = {
    token: "",
    user: [],
    colleagues: [],
    position: [],
    postUser: {
        name: "",
        email: "",
        phone: 0,
        position: 0,
        photo: ""
    },
    isLoadingUsers: true,
    counter: 6
};


export default createStore(
    reducer,
    state,
    applyMiddleware(promise, logger)
);